.video-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  max-width: 320px;
  border-bottom: 2px solid #666;
  margin-bottom: 20px;
  cursor: pointer;
}

.video-card:last-child {
  border: none;
}

p {
  margin: 0;
  margin-bottom: 20px;
}
